;IAChE,kBAAkB;EACpB;;EAEA;IACE,gEAAgE;EAClE;AACF","sourcesContent":["/* ChopsBrowserUI.css - Enhanced Styles with Preview Controls */\n\n.chops-browser-ui {\n  width: 100%;\n  height: 100%;\n  background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);\n  color: #e0e0e0;\n  font-family: -apple-system, BlinkMacSystemFont, \"Segoe UI\", \"Roboto\",\n    \"Helvetica Neue\", Arial, sans-serif;\n  display: flex;\n  flex-direction: column;\n  overflow: hidden;\n  position: relative;\n}\n\n/* Status and Error Indicators */\n.status-indicator {\n  position: absolute;\n  top: 10px;\n  right: 10px;\n  width: 12px;\n  height: 12px;\n  border-radius: 50%;\n  background: #4caf50;\n  z-index: 1000;\n  transition: all 0.3s ease;\n}\n\n.status-indicator.loading {\n  background: #ff9800;\n  animation: pulse 1.5s infinite;\n}\n\n.error-message {\n  position: absolute;\n  top: 10px;\n  left: 50%;\n  transform: translateX(-50%);\n  background: #f44336;\n  color: white;\n  padding: 8px 16px;\n  border-radius: 4px;\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  z-index: 1001;\n  animation: slideDown 0.3s ease;\n}\n\n.error-close {\n  background: none;\n  border: none;\n  color: white;\n  font-size: 16px;\n  cursor: pointer;\n  padding: 0;\n  margin-left: 8px;\n}\n\n@keyframes pulse {\n  0%,\n  100% {\n    opacity: 1;\n  }\n  50% {\n    opacity: 0.5;\n  }\n}\n\n@keyframes slideDown {\n  from {\n    transform: translateX(-50%) translateY(-100%);\n  }\n  to {\n    transform: translateX(-50%) translateY(0);\n  }\n}\n\n/* Header Section */\n.header-section {\n  display: flex;\n  align-items: center;\n  padding: 12px 20px;\n  background: rgba(0, 0, 0, 0.3);\n  border-bottom: 1px solid #444;\n  gap: 20px;\n  flex-shrink: 0;\n}\n\n.logo {\n  font-size: 18px;\n  font-weight: bold;\n  color: #4caf50;\n  white-space: nowrap;\n}\n\n.current-chord-display {\n  flex: 1;\n  text-align: center;\n}\n\n.chord-symbol {\n  font-size: 24px;\n  font-weight: bold;\n  color: #4caf50;\n  margin-bottom: 2px;\n}\n\n.chord-details {\n  font-size: 12px;\n  color: #888;\n}\n\n.total-samples {\n  color: #666;\n}\n\n.search-container {\n  min-width: 200px;\n}\n\n.search-input-wrapper {\n  position: relative;\n  display: flex;\n  align-items: center;\n}\n\n.search-input {\n  width: 100%;\n  padding: 8px 40px 8px 12px;\n  background: rgba(255, 255, 255, 0.1);\n  border: 1px solid #555;\n  border-radius: 20px;\n  color: #e0e0e0;\n  font-size: 14px;\n  outline: none;\n  transition: all 0.3s ease;\n}\n\n.search-input:focus {\n  border-color: #4caf50;\n  background: rgba(255, 255, 255, 0.15);\n}\n\n.search-input::placeholder {\n  color: #888;\n}\n\n.clear-search-btn {\n  position: absolute;\n  right: 25px;\n  background: none;\n  border: none;\n  color: #888;\n  cursor: pointer;\n  font-size: 16px;\n  padding: 0;\n  width: 16px;\n  height: 16px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.search-icon {\n  position: absolute;\n  right: 8px;\n  color: #666;\n  font-size: 12px;\n  pointer-events: none;\n}\n\n.header-buttons {\n  display: flex;\n  gap: 8px;\n}\n\n.chopsie-daisy-btn {\n  padding: 8px 16px;\n  background: linear-gradient(45deg, #9c27b0, #e91e63);\n  border: none;\n  border-radius: 4px;\n  color: white;\n  font-weight: bold;\n  cursor: pointer;\n  transition: all 0.3s ease;\n}\n\n.chopsie-daisy-btn:hover {\n  transform: translateY(-1px);\n  box-shadow: 0 4px 8px rgba(156, 39, 176, 0.3);\n}\n\n/* Main Content */\n.main-content {\n  flex: 1;\n  display: flex;\n  overflow: hidden;\n}\n\n.center-panel {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  overflow: hidden;\n}\n\n.center-panel.full-width {\n  width: 100%;\n}\n\n/* Piano Section */\n.piano-section {\n  flex-shrink: 0;\n  padding: 20px;\n  background: rgba(0, 0, 0, 0.2);\n  border-bottom: 1px solid #444;\n}\n\n.piano-controls {\n  display: flex;\n  gap: 12px;\n  margin-bottom: 16px;\n  justify-content: center;\n}\n\n.toggle-btn {\n  padding: 6px 12px;\n  background: rgba(255, 255, 255, 0.1);\n  border: 1px solid #555;\n  border-radius: 4px;\n  color: #e0e0e0;\n  cursor: pointer;\n  font-size: 12px;\n  transition: all 0.3s ease;\n}\n\n.toggle-btn:hover {\n  background: rgba(255, 255, 255, 0.15);\n}\n\n.toggle-btn.active {\n  background: #4caf50;\n  border-color: #4caf50;\n  color: white;\n}\n\n/* Piano Roll Styles */\n.piano-roll {\n  margin: 0 auto;\n  max-width: 600px;\n}\n\n.piano-container {\n  position: relative;\n  height: 120px;\n  background: #f5f5f5;\n  border-radius: 8px;\n  overflow: hidden;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);\n}\n\n.white-keys {\n  display: flex;\n  height: 100%;\n  position: relative;\n}\n\n.white-key {\n  flex: 1;\n  background: linear-gradient(to bottom, #ffffff 0%, #f0f0f0 100%);\n  border: 1px solid #ccc;\n  border-radius: 0 0 4px 4px;\n  cursor: pointer;\n  display: flex;\n  align-items: flex-end;\n  justify-content: center;\n  padding-bottom: 8px;\n  transition: all 0.1s ease;\n}\n\n.white-key:hover {\n  background: linear-gradient(to bottom, #f0f0f0 0%, #e0e0e0 100%);\n}\n\n.white-key.highlighted {\n  background: linear-gradient(to bottom, #4caf50 0%, #45a049 100%);\n  color: white;\n}\n\n.white-key.active {\n  background: linear-gradient(to bottom, #2196f3 0%, #1976d2 100%);\n  color: white;\n  transform: translateY(2px);\n}\n\n.black-keys {\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  height: 70%;\n  pointer-events: none;\n}\n\n.black-key {\n  position: absolute;\n  width: 3%;\n  height: 100%;\n  background: linear-gradient(to bottom, #333 0%, #111 100%);\n  border-radius: 0 0 4px 4px;\n  cursor: pointer;\n  pointer-events: auto;\n  display: flex;\n  align-items: flex-end;\n  justify-content: center;\n  padding-bottom: 4px;\n  color: white;\n  font-size: 10px;\n  transition: all 0.1s ease;\n  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);\n}\n\n.black-key:hover {\n  background: linear-gradient(to bottom, #555 0%, #333 100%);\n}\n\n.black-key.highlighted {\n  background: linear-gradient(to bottom, #4caf50 0%, #45a049 100%);\n}\n\n.black-key.active {\n  background: linear-gradient(to bottom, #2196f3 0%, #1976d2 100%);\n  transform: translateY(2px);\n}\n\n.note-label {\n  font-size: 10px;\n  font-weight: bold;\n  user-select: none;\n}\n\n.piano-info {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-top: 12px;\n  padding: 8px 12px;\n  background: rgba(0, 0, 0, 0.2);\n  border-radius: 4px;\n  font-size: 12px;\n  color: #888;\n}\n\n.active-notes {\n  display: flex;\n  align-items: center;\n  gap: 4px;\n}\n\n.notes-display {\n  color: #4caf50;\n  font-weight: bold;\n}\n\n.piano-hint {\n  font-style: italic;\n}\n\n/* Results Area */\n.results-area {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  overflow: hidden;\n  padding: 20px;\n}\n\n.results-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 16px;\n  padding-bottom: 8px;\n  border-bottom: 1px solid #444;\n}\n\n.results-count {\n  font-size: 14px;\n  color: #e0e0e0;\n}\n\n.loading-text {\n  color: #ff9800;\n  font-style: italic;\n}\n\n.inversion-indicator {\n  color: #888;\n  font-size: 12px;\n}\n\n.view-controls {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n}\n\n.filename-toggle {\n  background: none;\n  border: 1px solid #555;\n  border-radius: 4px;\n  padding: 4px 8px;\n  color: #888;\n  cursor: pointer;\n  font-size: 12px;\n  transition: all 0.3s ease;\n}\n\n.filename-toggle:hover {\n  border-color: #4caf50;\n  color: #4caf50;\n}\n\n.filename-toggle.active {\n  background: #4caf50;\n  border-color: #4caf50;\n  color: white;\n}\n\n.view-label {\n  font-size: 12px;\n  color: #888;\n}\n\n/* Results List */\n.results-list {\n  flex: 1;\n  overflow: hidden;\n}\n\n.results-empty {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  height: 200px;\n  text-align: center;\n  color: #666;\n}\n\n.empty-icon {\n  font-size: 4